.avatar {
  height: 40px;
  width: 40px;
}
.avatar .avatar-image,
.avatar .avatar-initials {
  height: 100%;
  width: 100%;
}
.avatar .avatar-initials {
  display: block;
  -webkit-background-size: 100% 100%;
  -moz-background-size: 100% 100%;
  background-size: 100% 100%;
  background-color: #aaa;
  color: #fff;
  font-size: 20px;
  line-height: 40px;
  font-family: "Helvetica Neue", Helvetica, "Hiragino Sans GB", Arial, sans-serif;
  text-align: center;
}
.avatar-large {
  height: 80px;
  width: 80px;
}
.avatar-large .avatar-initials {
  font-size: 40px;
  line-height: 80px;
}
.avatar-small {
  height: 30px;
  width: 30px;
}
.avatar-small .avatar-initials {
  font-size: 14px;
  line-height: 30px;
}
.avatar-rounded .avatar-image,
.avatar-rounded .avatar-initials {
  -webkit-border-radius: 5px;
  border-radius: 5px;
}
.avatar-circle .avatar-image,
.avatar-circle .avatar-initials {
  -webkit-border-radius: 50%;
  border-radius: 50%;
}
.avatar-hide-image .avatar-image {
  display: none;
}
.avatar-hide-initials .avatar-initials {
  display: none;
}
